@-webkit-keyframes LogoAnimation {
  0%   {height: 30px; width: 30px;}
  25%  {height: 35px; width: 35px;}
  50%  {height: 40px; width: 40px;}
  75%  {height: 45px; width: 45px;}
  85%  {height: 48px; width: 48px;}
  100% {height: 50px; width: 50px;}
}

@keyframes LogoAnimation {
  0%   {height: 30px; width: 30px;}
  25%  {height: 35px; width: 35px;}
  50%  {height: 40px; width: 40px;}
  75%  {height: 45px; width: 45px;}
  85%  {height: 48px; width: 48px;}
  100% {height: 50px; width: 50px;}
}

@-webkit-keyframes IconAnimations {
  0%   {height: 18px; width: 18px;}
  25%  {height: 21px; width: 21px;}
  50%  {height: 24px; width: 24px;}
  75%  {height: 28px; width: 28px;}
  100% {height: 30px; width: 30px;}
}

@keyframes IconAnimations {
  0%   {height: 18px; width: 18px;}
  25%  {height: 21px; width: 21px;}
  50%  {height: 24px; width: 24px;}
  75%  {height: 28px; width: 28px;}
  100% {height: 30px; width: 30px;}
}


